// Create custom element wrapper for Wix
class SophosicChatElement extends HTMLElement {
  constructor() {
    super();
    this.attachShadow({ mode: 'open' });
  }

  async connectedCallback() {
    try {
      // Create container in shadow DOM first
      const container = document.createElement('div');
      container.id = 'sophosic-chat-container-' + Math.random().toString(36).substr(2, 9);
      this.shadowRoot.appendChild(container);

      // Load Vue first
      await this.loadScript('https://unpkg.com/vue@3/dist/vue.runtime.global.prod.js');
      await this.waitForGlobal('Vue');

      // Load app script with correct path
      const appScript = isGitHubPages ? 
        'https://ezpzzz.github.io/sophia-web/js/app.js' : 
        '/sophia-web/js/app.js';
      await this.loadScript(appScript);
      await this.waitForGlobal('SophosicChat');

      // Get configuration
      const config = {
        apiUrl: this.getAttribute('api-url') || process.env.VUE_APP_API_URL,
        theme: {
          primary: this.getAttribute('primary-color') || '#5423E7',
          secondary: this.getAttribute('secondary-color') || '#1E1B3C'
        }
      };

      // Create a wrapper div for Vue mounting
      const wrapper = document.createElement('div');
      wrapper.setAttribute('data-v-app', '');
      container.appendChild(wrapper);

      // Mount the widget
      if (!window.Vue || !window.SophosicChat?.mount) {
        throw new Error('Required dependencies not loaded');
      }

      window.SophosicChat.mount(wrapper, config);
    } catch (err) {
      console.error('Failed to load chat widget:', err);
      this.shadowRoot.innerHTML = `
        <div style="color: red; padding: 1em; border: 1px solid red; margin: 1em;">
          Failed to load chat widget: ${err.message}
        </div>
      `;
    }
  }

  waitForGlobal(name, timeout = 2000) {
    return new Promise((resolve, reject) => {
      const start = Date.now();
      const check = () => {
        if (window[name]) {
          resolve();
        } else if (Date.now() - start > timeout) {
          reject(new Error(`Timeout waiting for ${name}`));
        } else {
          setTimeout(check, 50);
        }
      };
      check();
    });
  }

  loadScript(src) {
    return new Promise((resolve, reject) => {
      const existing = document.querySelector(`script[src="${src}"]`);
      if (existing) {
        resolve();
        return;
      }
      const script = document.createElement('script');
      script.src = src;
      script.onload = () => setTimeout(resolve, 100);
      script.onerror = reject;
      document.head.appendChild(script);
    });
  }
}

// Detect GitHub Pages environment
const isGitHubPages = window.location.hostname.includes('github.io');

// Only define if not already defined
if (!customElements.get('sophosic-chat-widget')) {
  customElements.define('sophosic-chat-widget', SophosicChatElement);
} 